.imageModal{
    width:100%;
    height: 290px;
    margin: 0;
    padding: 0;
    border-radius: 24px;
    margin-bottom: 15px;
}
.buttonClose{
    position: absolute;
    background-color:white;
    width: 24px;
    height: 24px;
    border:none;
    top:16px;
    right: 16px;
}
.modalSubtitleContainer{
    display:flex;
    width: 100%;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    color: #121417;
    font-family: Manrope, sans-serif;
    font-size: 18px;
    font-weight: 500;
    line-height: 24px;
    margin-bottom: 8px;
}
.modalSubtitleText,
.modalSubtitleTextModel{
    margin-right: 4px;
}
.modalSubtitleTextModel{
    color: #3470FF;
}
.modalContainerFirst{
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: flex-start;
    align-items: center;
}
.modalTextFirst{
    color: rgba(18, 20, 23, 0.50);
    font-family: Manrope, sans-serif;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    margin-right: 6px;
}
.modalDescription{
    color: #121417;
    font-family: Manrope, sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    /* line-height: 20px; */
    margin: 0;
    margin-top: 14px;
}
.modalAccessoriesTitle{
    color: #121417;
    font-family: Manrope, sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    margin: 0;
    margin-bottom: 8px;
    margin-top: 24px;
    margin-bottom: 24px;
}
.modalAccessory{
   display: flex;
   flex-direction: row;
   margin: 0;
 }
.rentalConditions{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 8px;   
}
.modalConditionsItem,
.modalMilage{
    padding: 7px 14px;
    border-radius: 35px;
    background: #F9F9F9;
    color: #363535;
    font-family: Montserrat, sans-serif;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    margin: 0;
}
.modalMileage{
    color:#3470FF;
    font-family: Montserrat, sans-serif;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: 18px;
    letter-spacing: -0.24px;
}
.buttonRentalCar{
    text-decoration: none;
    display: inline-flex;
    width: 168px;
    height: 44px;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
    background: #3470FF;
    border: none;
    font-family: Manrope, sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 20px;
    color: white;
    margin-top: 24px;
}
.buttonRentalCar:hover,
.buttonRentalCar:focus{
    background: #0B44CD;
    cursor: pointer;
}
